.background {
  background-color: black;
  height: 100%;
}

.container {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -ms-flex-pack: center;
      justify-content: center;
}

@-webkit-keyframes startSwing {
  from { -webkit-transform: translate(-50%, -25%); transform: translate(-50%, -25%) }
  to { -webkit-transform: translate(-50%, 25%); transform: translate(-50%, 25%) }
}

@keyframes startSwing {
  from { -webkit-transform: translate(-50%, -25%); transform: translate(-50%, -25%) }
  to { -webkit-transform: translate(-50%, 25%); transform: translate(-50%, 25%) }
}

@-webkit-keyframes startBlinking {
  50% { opacity: 0 }
}

@keyframes startBlinking {
  50% { opacity: 0 }
}

.start-button {
  -webkit-animation: startSwing .3s ease-in-out alternate both infinite;
          animation: startSwing .3s ease-in-out alternate both infinite;
  background-color: transparent;
  border: 0;
  bottom: 4rem;
  color: inherit;
  cursor: pointer;
  font-family: inherit;
  font-size: 1.5rem;
  left: 50%;
  padding: 0;
  opacity: .8;
  position: absolute;
  -webkit-transition: opacity .3s ease-in-out;
  -o-transition: opacity .3s ease-in-out;
  transition: opacity .3s ease-in-out;
}

.start-button:hover {
  opacity: 1;
}
.start-button:focus {
  outline: none;
}

.start-button.blinking {
  -webkit-animation: startBlinking .3s ease-in-out alternate both infinite;
          animation: startBlinking .3s ease-in-out alternate both infinite;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}



